// Callout Block Styles
.callout-block {

    h2 {
        font-weight: var(--font-weight-bold);
        color: var(--black);
        font-size: var(--font-size-18);
    }

    p {
        @include font-body-18;
        color: var(--black);
        margin: rem(4px) 0 0;
    }

    a.button {
        @include btn(var(--black));
        margin-top: rem(16px);

        @include bp-max(mobile) {
            ::after {
                font-size: 16px;
                vertical-align: top;
            }
        }
    }    

    .cloud {
        background-color: var(--aten-blue-light);
    }

    .gold {
        background-color: var(--aten-yellow-light);
    }

    .navy {
        background-color: var(--aten-blue-dark);

        p,
        h2 {
            color: var(--white);
        }
    }

    .large {
        padding: rem(24px);
        border-radius: var(--default-border-radius);

        @include bp-min(tablet) {
            padding: rem(40px) rem(40px) rem(20px);
            border-radius: rem(4px) rem(4px) 0 0;
            background-size: 100%;
            background-repeat: no-repeat;
            background-position: bottom center;
        }

        @include bp-min(smdesk) {
            padding: rem(60px) rem(60px) rem(32px);
        }

        a.button {
            @include btn-large(var(--white));

            @include bp-min(tablet) {
                margin-top: rem(20px);
            }

            @include bp-max(mobile) {
                &::after {
                    font-size: rem(20px);
                    vertical-align: top;
                }
            }
        }

        p {
            @include bp-min(tablet) {
                margin-top: rem(20px);
                font-size: var(--font-size-h3);
            }
        }


        h2 {
            @include bp-min(tablet) {
                font-size: var(--font-size-h2);
            }
        }

        &.cloud {
            @include bp-min(tablet) {
                background-image: url('../aten-fse/assets/img/swishies_sky-blue.svg');
            }

            a.button {
                @include btn-large(var(--navy));

                @include bp-max(mobile) {
                    &::after {
                        font-size: rem(20px);
                        vertical-align: top;
                    }
                }
            }    
        }

        &.gold {
            @include bp-min(tablet) {
                background-image: url('../aten-fse/assets/img/swishies_gold.svg');
            }    
        }

        &.navy {
            @include bp-min(tablet) {
                background-image: url('../aten-fse/assets/img/swishies_black.svg');
            }
        }
    }

    .small {
        padding: rem(24px);
        border-radius: var(--default-border-radius);

        @include bp-min(tablet) {
            padding: rem(32px);

            h2 {
                font-size: var(--font-size-h4);
            }
        }

        @include bp-min(lgdesk) {
            max-width: 75%;
            margin-left: auto;
            margin-right: auto;
        }

        a.button {
            @include btn(var(--black));
            padding-left: 0px!important;

            @include bp-max(mobile) {
                &::after {
                    font-size: rem(20px);
                    vertical-align: top;
                }
            }

            border: 0;
            background-color: transparent;
        }    

        &.navy {
            a.button {
                @include btn(var(--white));
                padding-left: 0px!important;

                @include bp-max(mobile) {
                    &::after {
                        font-size: rem(20px);
                        vertical-align: top;
                    }
                }
            }    
        }
    }

    .callout-wave {
        width: 100%;

        @include bp-max(tablet) {
            display: none;
        }
    
        img {
            width: 100%;
            height: auto;
        }
    }
}

