// Column Block Styles
.column-block {

    ul {
        list-style-type: none;
        padding: 0;
        margin: 0;
        display: flex;
        gap: rem(28px);
        margin-bottom: rem(28px);

        @include bp-min(tablet) {
            gap: rem(4px);
            margin-bottom: rem(4px);
        }
    }

    .column-block-column {
        overflow: hidden;
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center calc(100% - rem(28px));
        border-radius: var(--default-border-radius);

        @include bp-min(tablet) {
            background-position: center calc(100% - rem(10px));
        }

        @include bp-min(lgdesk) {
            background-position: center calc(100% - rem(15px));
        }

        &:nth-child(1) {
            background-color: var(--aten-gray);
            background-image: url('../aten-fse/assets/img/column-swishies_bright-blue.svg');
        }

        &:nth-child(2) {
            background-color: var(--aten-orange-dark);
            background-image: url('../aten-fse/assets/img/column-swishies_tangerine.svg');
        }

        &:nth-child(3) {
            background-color: var(--aten-green);
            background-image: url('../aten-fse/assets/img/column-swishies_light-green.svg');
        }

        > .wave-border {
            background-color: var(--white);
        }

        &.single-column {
            background-position: center calc(100% - rem(28px));

            @include bp-min(tablet) {
                background-position: center calc(100% - rem(70px));
            }
    
            @include bp-min(lgdesk) {
                background-image: url('../aten-fse/assets/img/column-swishies_bright-blue_large.svg');
                background-position: center calc(100% - rem(80px));
            }
        }
    }

    h2 {
        @include font-h3;
        font-size: var(--font-size-h4);
        font-weight: var(--font-weight-bold);
        text-indent: 0;

        @include bp-min(smdesk) {
            // Diverges from standard h3 sizing
            font-size: rem(24px);
            line-height: rem(32px);
        }
    }

    p {
        text-indent: 0;
        @include font-body-18;
        margin: 0;

        a {
            font-weight: var(--font-weight-bold);
        }
    }

    .column-block-content {
        padding: rem(24px);

        > * {
            margin-bottom: rem(16px);

            &:last-child {
                margin-bottom: 0;
            }
        }

        @include bp-min(tablet) {
            padding: rem(32px);
        }
    }

    ul:not(.column-layout-1) .column-block-content {
        @include bp-min(tablet) {
            // height: 100%;
        }
    }

    .column-block-image-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: rem(230px);
        overflow: hidden;

        img {
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }

    .wave-border {
        display: none;

        @include bp-min(tablet) {
            display: block;
        }

        @include bp-min(lgdesk) {
            max-width: 100%;
            margin: 0 auto;
        }

        &,
        img {
            width: 100%;
        }
    }


    a.button {
        text-indent: 0;
        padding-right: rem(36px);
        position: relative;

        @include bp-min(tablet) {
            padding-right: rem(48px);           
        }

        &::after {
            display: none;
        }   

        .button-icon {
            @include font-material-icons;
            position: absolute;
            color: var(--white);
            content: "arrow_circle_right";
            display: inline;
            font-size: rem(20px);
            margin-left: rem(6px);

            @include bp-min(tablet) {
                font-size: rem(24px);
                margin-left: rem(8px);
            }
        }
    }

    // Single column layout
    .column-layout-1 {
        @include bp-min(tablet) {
            margin-bottom: 0;

            .column-block-content {
                padding: rem(48px);
            }

            .column-block-image-wrapper {
                height: rem(430px);
            }
        }

        @include bp-min(lgdesk) {
            .column-block-column{
                max-width: 100%;
                margin: 0 auto;
            }

            .column-block-image-wrapper {
                height: rem(490px);
            }
        }
    }

    // Two column layout
    .column-layout-2 {
        flex-direction: column;

        @include bp-min(tablet) {
            flex-direction: row;
            flex-wrap: nowrap;

            .column-block-column {
                width: calc(50% - rem(2px));
            }
        }

        @include bp-min(lgdesk) {
            .column-block-image-wrapper {
                height: rem(320px);
            }
        }
    }

    // Three column layout
    .column-layout-3 {
        flex-direction: column;

        @include bp-between(tablet, lgdesk) {
            .column-block-column {
                background-image: none;
                display: flex;
                flex-direction: row-reverse;
                justify-content: space-between;
                width: 100%;

                &:not(.with-image) {
                    flex-direction: row;
                }
            }

            .column-block-image-wrapper {
                width: rem(250px);
                height: rem(180px);
                flex-shrink: 0;
            }
        }


        @include bp-min(lgdesk) {
            flex-direction: row;
            flex-wrap: nowrap;

            .column-block-column {
                width: calc(33.3% - rem(2px));
            }

            .column-block-image-wrapper {
                height: rem(214px);
            }
        }
    }
}