// Page header with subtitle styles
.page-header-component {
    margin-bottom: rem(24px);
    position: relative;
    z-index: 2;
    overflow: hidden;

    @include bp-min(tablet) {
        margin-bottom: rem(40px);
    }

    @include bp-min(mddesk) {
        margin-bottom: rem(60px);
    }

    .page-header-block {
        background-size: cover;
        background-position: bottom left;
        background-repeat: no-repeat;
        padding: rem(24px) rem(15px);
        background-color: var(--aten-blue-light);
    
        @include bp-min(tablet) {
            padding: rem(32px) rem(40px) 0;
            background-color: var(--transparent);
            background-image: linear-gradient(to right, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0)), url('../aten-fse/assets/img/header-wave.svg'); 
        }
    
        @include bp-min(smdesk) {
            padding: rem(57px) rem(68px) 0 rem(40px);
        }
    
        @include bp-min(mddesk) {
            padding: rem(80px) rem(60px) 0;
        }
        
        @include bp-min(lgdesk) {
            padding: rem(80px) rem(120px) rem(34px);
        }

        .header-content {
            position: relative;
          
            @include bp-min(lgdesk) {
                max-width: rem(1444px);
                margin-left: auto;
                margin-right: auto;
            }
          
            .header-subtitle {
                @include bp-min(lgdesk) {
                    max-width: rem(960px);
                }
            }
        }
        
        h1 {
            font-weight: var(--font-weight-bold);
            color: var(--aten-blue-dark);
        }
    
        p {
            color: var(--aten-gray-dark);
            font-size: var(--font-size-h3);
            margin-top: rem(8px);
            margin-bottom: 0;
    
            @include bp-min(tablet) {
                margin-top: rem(24px);
            }
            
            @include bp-min(lgdesk) {
                font-size: rem(32px);
            }

            &.header-publication-date {
                @include font-label-20;
                font-weight: var(--font-weight-bold);
                margin-top: 0;
                margin-bottom: rem(8px);

                @include bp-min(tablet) {
                    margin-bottom: rem(24px);
                }
            }
        }
    
        hr {
            width: rem(160px);
            border: 0;
            margin: 0;
    
            @include bp-min(tablet) {
                border-bottom: rem(8px) solid var(--gold);
                margin-top: rem(32px);
                margin-bottom: rem(8px);
            }

            @include bp-min(smdesk) {
                margin-top: rem(48px);
            }
        }
    
        &.header-with-image {
            background-size: cover;
            background-position: center;
            padding: rem(20px) rem(15px);
            background-color: var(--aten-blue-dark);
    
            @include bp-min(tablet) {
                padding: rem(12px) rem(40px) rem(40px);
            }
        
            @include bp-min(smdesk) {
                padding: rem(338px) rem(80px) rem(80px);
                background-color: var(--transparent);
            }
        
            @include bp-min(mddesk) {
                padding: rem(280px) rem(80px) rem(60px);
            }
            
            @include bp-min(lgdesk) {
                padding: rem(280px) rem(120px) rem(60px);
            }
    
            @include bp-max(smdesk) {
                // The important here is necessary to override the dynamic WP featured image background from the header component
                background-image: none!important;
            }
    
            h1, 
            p {
                color: var(--white);
            }

            hr {
                @include bp-min(tablet) {
                    margin-top: 0;
                    margin-bottom: rem(12px);
                }

                @include bp-min(smdesk) {
                    margin-top: 0;
                    margin-bottom: rem(20px);
                }
            }
        }
    }

    .mobile-image {
        width: 100%;
        height: 220px;
        position: relative;
        overflow-y: hidden;
        display: flex;
        align-items: center;
        justify-content: center;

        @include bp-min(tablet) {
            height: 300px;
        }

        @include bp-min(smdesk) {
            display: none;
        }

        img {
            width: 100%;
            height: auto;
        }

        .mobile-image-overlay {
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;

            @include bp-min(tablet) {
                background-image: linear-gradient(to top, rgba(28, 63, 148, 1), rgba(28, 63, 148, 0));
            }
        }
    }

    .edge-wave {
        width: 102%;
        height: 0px;
        position: relative;
        left: -1%;
        top: rem(-2px);

        @include bp-min(tablet) {
            height: auto;
        }
    }

    .header-swishies-wrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        background: linear-gradient(to right, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0)), url('../aten-fse/assets/img/header-swishies.svg');
        background-position: center rem(20px);
        background-size: 100%;
        background-repeat: no-repeat;

        @include bp-max(tablet) {
            display: none;
        }
    }
}