// Functions
@import '_functions/_functions';

// Mixins
@import '_mixins/_mixins';

// Import settings
@import '_settings/_settings';

// Broad element styling
@import 'elements/_elements';

// Individual components
@import 'components/_components';

// Page templates
@import 'templates/_templates';

// Set default full window width
:root {
  --windowWidth: 1620px;

  // On largest screens take windowWidth less container width
  // divide to get 1 side beyond container width, this is used
  // so that placement is correct for images beyond container
  --full-left: calc(calc(var(--windowWidth) - 1400px) / -2);
  --full-right: calc(calc(var(--windowWidth) - 1400px) / -2);
}

// Utility class for screen-reader only content
.a11y-visible {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
