.btn-large--navy,
.wp-block-button .wp-block-button__link {
  background-color: var(--default-button-ci);
  border: none;
  border-radius: var(--default-border-radius);
  color: var(--white);
  display: block;
  font-family: var(--font-family-sans);
  font-size: var(--font-size-body-18);
  font-weight: var(--font-weight-bold);
  line-height: 156%;
  padding: 0.5rem 2.25rem 0.5rem 1rem;
  text-align: left;
  text-decoration: none;
  transition-property: all;
  transition-duration: 150ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  width: fit-content;
  text-indent: 0;
  position: relative;
}
@media (min-width: 768px) {
  .btn-large--navy,
  .wp-block-button .wp-block-button__link {
    padding-right: 3rem;
  }
}
.btn-large--navy a,
.wp-block-button .wp-block-button__link a {
  color: var(--white);
  text-decoration: none;
}
.btn-large--navy::after,
.wp-block-button .wp-block-button__link::after {
  font-family: var(--font-family-icons);
  font-weight: var(--font-weight-regular);
  text-decoration: none;
  text-transform: none;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-feature-settings: "liga";
  font-feature-settings: "liga";
  color: var(--white);
  content: "arrow_circle_right";
  display: inline;
  position: absolute;
  font-size: 1.5rem;
  margin-left: 0.5rem;
}
@media (max-width: 767.98px) {
  .btn-large--navy::after,
  .wp-block-button .wp-block-button__link::after {
    font-size: 1.25rem;
    margin-left: 0.375rem;
  }
}
.btn-large--navy:hover, .btn-large--navy:focus,
.wp-block-button .wp-block-button__link:hover,
.wp-block-button .wp-block-button__link:focus {
  background-color: var(--default-button-hover-ci);
  color: var(--white);
  text-decoration: underline;
  transition-property: all;
  transition-duration: 150ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (max-width: 767.98px) {
